.button {
  height: 56px;
  width: 100%;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;

  color: #fff;
  text-align: center;
  // padding-inline: 48px;

  background-color: $color-orange;

  border-radius: 8px;
  border: none;
  cursor: pointer;

  @include onDesktop {
    width: 324px;
  }

  @include hover(background-color, $main-color);

  &:active {
    background-color: #010810;
  }

  &--send {
    background-color: $main-color;

    @include onDesktop {
      width: 100%;
    }
    
    @include hover(background-color, $color-orange);
  }
}
